import React from 'react'
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { OnOff } from '../helpComponents/OnOff'



const mapStateToProps = (state) => {
    return {
        currentIframe: state.iframe.currentIframe,

    };
}

const mapDispatchToProps = (dispatch) => ({

    setCodeComponentIframe: (text) => dispatch(actions.setCodeComponentIframe(text)),
    setIframeTitle: (title) => dispatch(actions.setIframeTitle(title)),
    setIframeShow: () => dispatch(actions.setIframeShow()),
})

export default connect(mapStateToProps, mapDispatchToProps)(function IframeNav(props) {
    const { currentIframe, setCodeComponentIframe,setIframeShow,setIframeTitle } = props;


    return (

        <div className='container-fluid'>
            <div className="row ">
                <label className="title">Iframe Settings</label>
            </div>
            <div className="row ">
                <input className="col-8 p-0 input"
                    value={currentIframe.titleIframe}
                    onChange={(e) => { setIframeTitle(e.target.value) }}
                ></input>
                <div className="col-4 p-0 d-flex justify-content-end">
                    <OnOff
                        idElement="IframeShow"
                        cheak={currentIframe.iframeShow}
                        onChangeOnOff={setIframeShow}
                        type={"IframeShow"}
                    ></OnOff>
                </div>
            </div>
            <div className="row mt-3">
                <label className="title">Code Html</label>
            </div>
            <div className="row mt-1">
                <textarea
                    className="form-control p-0  m-0 textAgentDetailsTextArea texteara-border"
                    placeholder="Enter Code HTML"
                    onChange={(e) => setCodeComponentIframe(e.target.value)}
                ></textarea>
            </div>
        </div>
    );

});

