import React from 'react'
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';



const mapStateToProps = (state) => {
    return {
        currentIframe:state.iframe.currentIframe,
    };
}

const mapDispatchToProps = (dispatch) => ({


})

export default connect(mapStateToProps, mapDispatchToProps)(function IframeComponent(props) {
    const { currentIframe, screenSize } = props;


    return (
        <div className='row d-flex justify-content-center'>
            {currentIframe.iFrameCard.codeComponentIframe ?
                <div className='row d-flex justify-content-center'
                    dangerouslySetInnerHTML={{ __html: currentIframe.iFrameCard.codeComponentIframe }} ></div>
                : ""}
        </div>
    );

});

