import produce from 'immer';
import createReducer from "./reducerUtils";

const initialState = {

    currentIframe: {
        iframeShow: true,
        titleIframe: "Iframe",
        iFrameCard: {
            codeComponentIframe: "",
        },
    }
};
const iframe = {
    setCurrentIframe(state, action) {
        state.currentIframe = action.payload;
    },
    
    setIframeShow(state, action) {
        state.currentIframe.iframeShow = !state.currentIframe.iframeShow;
    },
    setIframeTitle(state, action) {
        state.currentIframe.titleIframe = action.payload;
    },
    setCodeComponentIframe(state, action) {
        state.currentIframe.iFrameCard.codeComponentIframe = (action.payload);
    },

}
export default produce((state, action) => createReducer(state, action, iframe), initialState);
